.App {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 500px;
  
}
/* #root{
  background-color: #87CEEB;
} */
#display{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  border-radius: 8px;
  padding-top: 20px;
}
.sound{
  border-radius: 5px;
  outline: #87CEEB solid 3px;
  background-color: white;
  font-size: 19px;
  color: #f8abb8;
  width: 80px;
  height: 40px;
  border: none;
}
#drum-machine{
  display: flex;
  align-items: center;
  height: 100vh;
  flex-direction: column;
}
.animal-data{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
img{
  outline: solid #87CEEB 10px;
  border-radius: 4px;
}
header{
  font-family:Verdana, Geneva, Tahoma, sans-serif;

}
.animal-class{
  display: flex;
}
@media screen and (max-width : 600px){
.animal-class{
  display: flex;
  flex-direction: column;
}
header{
  font-size: 13px;
}
}
#kids{
  font-size: 20px;
  color: #FFC0CB;
  font-weight: 600;
}
.kids{
  color: #6be06b;
  font-weight: 600;
}
.click{
  color: #87CEEB;
}
.click-span{
  background-color: #fdd2d9;
  padding: 2px;
  border-radius: 3px;


}